










import Vue from 'vue';
import WindowFrame from '@/components/layout/WindowFrame.vue';

export default Vue.extend({
    components: {
        WindowFrame,
    },
    data() {
        return {
            time: '',
            date: '',
        };
    },
    methods: {
        zeroPadding(num, digit): string {
            let zero = '';
            for (let i = 0; i < digit; i++) {
                zero += '0';
            }
            return `${zero}${num}`.slice(-digit);
        },
        updateTime(): void {
            const cd = new Date();
            const week = ['SUN', 'MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT'];
            this.time = `${this.zeroPadding(cd.getHours(), 2)}:${this.zeroPadding(
                cd.getMinutes(),
                2
            )}:${this.zeroPadding(cd.getSeconds(), 2)}`;
            this.date = `${this.zeroPadding(cd.getDate(), 2)}-${this.zeroPadding(
                cd.getMonth() + 1,
                2
            )}-${this.zeroPadding(cd.getFullYear(), 4)} ${week[cd.getDay()]}`;
        },
    },
    mounted() {
        setInterval(() => {
            this.updateTime();
        }, 500);
    },
    computed: {
        themeColor(): string {
            return this.$store.state.themeColor;
        },
        style(): string {
            return `background: ${this.themeColor};background: radial-gradient(ellipse at center, ${this.themeColor} 0%, #000000 97%)`;
        },
    },
});
