



















import Vue from 'vue';

export default Vue.extend({
    name: 'app-bar',
    props: {
        hideSystemBar: {
            type: Boolean,
            required: false,
            default: false,
        },
    },
    methods: {
        goHome(): void {
            this.$router.push('/');
        },
    },
    computed: {
        themeColor(): string {
            return this.$store.state.themeColor;
        },
        icon(): string {
            return this.$route.meta.icon;
        },
        text(): string {
            return this.$route.meta.text;
        },
    },
});
